.hot-address { font-size: q(26); color: #666;
    .hot-title { line-height: q(88); padding-left: q(48); background: #f1f0f6; border-bottom: 1px solid #e6e6e6;}
    .hot-list { padding: 0 q(27) q(35);
        .hot-city { padding: 0 q(21); border-bottom: 1px dotted #c3c3c3;
            .city-name { display: block; line-height: q(88); max-width: 100%; color: #666; overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}
        }
    }
}
